function pemToArrayBuffer(pem) {

    const binary = atob( publicKeyPEM );

    // Convert binary string to ArrayBuffer
    const buffer = new ArrayBuffer(binary.length);
    const view = new Uint8Array(buffer);
    for (let i = 0; i < binary.length; i++) {
        view[i] = binary.charCodeAt(i);
    }
    return buffer;
}

function base64UrlToUint8Array(base64Url) {
    const base64 = base64Url
        .replace(/-/g, '+')
        .replace(/_/g, '/')
        .padEnd(base64Url.length + (4 - base64Url.length % 4) % 4, '=');
    const binaryString = atob(base64);
    const bytes = new Uint8Array(binaryString.length);
    for (let i = 0; i < binaryString.length; i++) {
        bytes[i] = binaryString.charCodeAt(i);
    }
    return bytes;
}

async function verifyJWS(token, publicKeyPEM) {
    try {
        // Convert the PEM key to an ArrayBuffer
        const keyData = pemToArrayBuffer(publicKeyPEM);

        // Import the public key
        const key = await crypto.subtle.importKey(
            'spki',
            keyData,
            {
                name: 'RSASSA-PKCS1-v1_5',
                hash: 'SHA-256'
            },
            true,
            ['verify']
        );

        // Extract the JWS parts
        const parts = token.split('.');
        const header = atob(parts[0]);
        const payload = atob(parts[1]);
        const signature = base64UrlToUint8Array(parts[2]);

        // Create the data to verify (header + '.' + payload)
        const data = new TextEncoder().encode(`${parts[0]}.${parts[1]}`);

        // Verify the signature
        const isValid = await crypto.subtle.verify(
            {
                name: 'RSASSA-PKCS1-v1_5'
            },
            key,
            signature,
            data
        );

        if (isValid) {
            return JSON.parse(payload);
        } else {
            throw new Error('Invalid or expired JWS');
        }
    } catch (error) {
        throw new Error(`Error verifying JWS: ${error.message}`);
    }
}


